<template>
 <div class="chooseBeCared">
    <div class="page-header-main">
      <mt-header v-if="fromIndex">
        <router-link to="/index" slot="left">
          <mt-button icon="back">选择被护理人</mt-button>
        </router-link>
      </mt-header>
      <mt-header v-else>
        <router-link to="/index/mymenu" slot="left">
          <mt-button icon="back">选择被护理人</mt-button>
        </router-link>
      </mt-header>
    </div>
    <div>
      <BeCaredItem v-for="beCaredInfo in beCaredList.beCaredList" :beCaredInfos="beCaredInfo" :serviceType="serviceType"></BeCaredItem>
    </div>
    <div class="footer">
      <mt-button size="large" @click.native="handleAddBeCaredClick">添加被护理人</mt-button>
    </div>
    <mt-actionsheet :actions="changeCitiesAction" v-model="sheetVisible2" cancel-text=""></mt-actionsheet>
  </div>
</template>

<script>
import BeCaredItem from '../components/BeCaredItem';
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'chooseBeCared',
  data () {
    return {
      fromIndex: true,
      serviceType: '',
      sheetVisible2: false,
      changeCitiesAction: [],
    }
  },
  components: {
    BeCaredItem
  },
  computed: {
    ...mapGetters({
      beCaredList: 'getBeCaredList',
      cities: 'getCities',
      globalVal: 'getGlodalVal'
    }),
    'changeCitiesAction': function() {
      const ctx = this;
      let changeCitiesAction = [];
      this.cities.forEach(i => {
            let obj = {};
            obj.name = i.city;
            obj['method'] = function() {
              ctx.changeCurrentCity(i.city);
            }
            changeCitiesAction.push(obj);
          })
      return changeCitiesAction;
    }
  },
  methods: {
    ...mapActions({
      fetchBeCared: 'fetchBeCared',
      changejrOrJx: 'changejrOrJx',
      becaredStatus: 'becaredStatus',
      fetchCities: 'fetchCities',
      changeCurrentCity: 'changeCurrentCity',
      changeInit: 'changeInit'
    }),
    handleAddBeCaredClick: function() {
      this.becaredStatus(true);
      this.$router.push({path: '/changeBeCared', query: { new: 'true' }})
    }
  },
  beforeRouteEnter(to, from, next) {
    if(to.query.clickType == 'index') {
      next(vm => {
        vm.fromIndex = true;
        vm.sheetVisible2 = true;
      })
    } else {
      next(vm => {
        vm.fromIndex = false;
        vm.sheetVisible2 = false;
      })
    }
    if(to.query.serviceType == 'patient') {
      next(vm => {
        vm.serviceType = 'patient';
        vm.changejrOrJx('计日类');
      })
    }
    if(to.query.serviceType == 'muyin') {
      next(vm => {
        vm.serviceType = 'muyin';
        vm.changejrOrJx('计日类');
      })
    }
    if(to.query.serviceType == 'jixiang') {
      next(vm => {
        vm.serviceType = 'jixiang';
        vm.changejrOrJx('计项类');
      })
    }
    if(to.query.serviceType == 'yuesao') {
      next(vm => {
        vm.serviceType = 'yuesao';
        vm.changejrOrJx('计日类');
      })
    }
  },
  created() {
    this.fetchBeCared().catch(error => {
      if(error.body.code == 3001) {
        this.$router.push('/');
      }
    })
    if(!this.fromIndex) {
      this.sheetVisible2 = false;
    }
    this.fetchCities('Bearer ' + this.privateToken).then(response => {
      if(this.globalVal.init == 'true') {
        this.changeCurrentCity(response.data[0].city);
        this.changeInit('false');
      }
    }, error => {
      if(error.body.code == 3001) {
        this.$router.push('/');
      }
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
.mint-header {
  background-color: #fff;
  color: black;
  margin-bottom: 3%;
}
.chooseBeCared {
  width: 100%;
  height: 100%;
  background-color: #eae5e5;
}
.footer {
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
}
.mint-button::after {
  background-color: none;
}
.chooseBeCared .mint-actionsheet {
  max-height: 11.5rem;
  overflow: scroll;
}
</style>



