<template>
  <!-- <div>{{beCaredInfo.name}}</div> -->
  <div class="beCaredItem">
    <div class="beCaredDetail" @click="evaluate(beCaredInfos.age)">
      <p class="beCaredItemHeader">
        <span>{{beCaredInfos.name}}</span>
        <span>{{beCaredInfos.phone}}</span>
      </p>
      <p class="beCaredItemBody">
        <span>家属&nbsp;{{beCaredInfos.family}}</span>
        <span>家属联系方式&nbsp;{{beCaredInfos.familyPhone}}</span>
      </p>
    </div>
    <div class="beCaredItemFoot">
      <mt-button size="normal" @click.native="editBeCared(beCaredInfos.id)"><img src="../assets/icon_edit.png" alt="" >编辑</mt-button>
      <mt-button @click.native="showBox(beCaredInfos.id)" size="normal"><img src="../assets/icon_del.png" alt="">删除</mt-button>
    </div>
    <div class="msgbox" v-if="show">
      <div class="m_header">
        <div class="m_title">提示</div>
      </div>
      <div class="m_content">
        <div class="m_message">
          <p>确认删除?</p>
        </div>
      </div>
      <div class="m_btns">
        <button @click="handleCancle" class="btn btn_cancel">取消</button>
        <button @click="handleRight" class="btn btn_right">确定</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import { MessageBox,Toast } from 'mint-ui';
import {checkError} from '../utils/util'
export default {
  name: 'BeCaredItem',
  props: ['beCaredInfos', 'serviceType'],
  data() {
    return {
      show: false,
      id: ''
    }
  },
  computed: {
    ...mapGetters({
      globalVal: 'getGlodalVal'
    })
  },
  methods: {
    ...mapActions({
      changeBecaredId: 'changeBecaredId',
      deleteBeCare: 'deleteBeCare',
      fetchBeCared: 'fetchBeCared',
      becaredStatus: 'becaredStatus'
    }),
    editBeCared: function(id) {
      this.becaredStatus(false);
      this.changeBecaredId(this.beCaredInfos.id)
      this.$router.push({path: `/changeBeCared?serviceType=${this.serviceType}`, query: { new: 'false',id: id }})
    },
    evaluate: function(age) {
      if(this.serviceType == 'patient') {
        this.$router.push({path: '/patientEvaluate'})
      }
      if(this.serviceType == 'muyin') {
        this.$router.push({path: '/muYinEvaluate',query: { age: age }})
      }
      if(this.serviceType == 'jixiang') {
        this.$router.push({path: '/jiXiangEvaluate'})
      }
      if(this.serviceType == 'yuesao') {
        this.$router.push({path: '/muYinEvaluate',query: { age: age, type: 'ys'}})
      }
      this.changeBecaredId(this.beCaredInfos.id)
    },
    showBox: function(id) {
      this.id = id;
      this.show = true;
    },
    handleCancle: function() {
      this.show = false;
    },
    handleRight: function() {
      this.deleteBeCare(this.id).then(response => {
        this.fetchBeCared();
        this.show = false;
      }).catch(error => {
        const message = checkError(error);
        Toast({
            message: message,
            position: 'bottom',
            duration: 1000
          });
      })
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.beCaredItemHeader, .beCaredItemBody,.beCaredItemFoot{
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
span {
  margin-left: 2%;
  margin-right: 2%;
}
.beCaredItemBody {
  padding-top: 20px;
  font-size: 0.4rem;
  color: #a19ba1;
  padding-bottom: 20px;
}
.beCaredItem {
  background-color: #fff;
}
.beCaredItemHeader {
  padding-top: 50px;
  font-size: 0.5rem;
  color: #565256;
}
.beCaredItemFoot .mint-button {
  width: 50%;
  border-radius: 0px;
  background-color: #fff;
}
.beCaredItem img {
  width: 0.4rem;
  vertical-align: middle;
  margin-top: -6px;
  margin-right: 10px;
}
.beCaredDetail {
  display: block;
}
</style>



